.nav-bar {
    background: #181818;
    width: 60px;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 3;
    min-height: 500px;

    .logo {
        display: block;
        padding: 0 8px;

        img {
            display: block;
            margin: 8px auto;
            width: 24px;
            height: auto;

            &.sub-logo {
                width: 60px;
                margin-left: -8px;
            }
        }
    }

    nav {
        display: block;
        text-align: center;
        position: absolute;
        height: 210px;
        top: 50%;
        margin-top: -120px;
        width: 100%;

        a {
            font-size: 22px;
            color: #4d4d4e;
            display: block;
            line-height: 51px;
            height: 51px;
            position: relative;
            text-decoration: none;

            i {
                transition: all 0.3 ease-out;
            }

            &:hover {
                color: #ffd700;

                svg {
                    opacity: 0;
                }

                &:after {
                    opacity: 1;
                }
            }

            &:after {
                 content: '';
                 font-size: 9px;
                 letter-spacing: 2px;
                 position: absolute;
                 bottom: 0;
                 display: block;
                 width: 100%;
                 text-align: center;
                 opacity: 0;
                 transition: all 0.3s ease-out;
            }

            &:nth-child(1) {
                &:after {
                    content: 'HOME';
                }
            }

            &:nth-child(2) {
                &:after {
                    content: 'ABOUT';
                }
            }

            &:nth-child(3) {
                &:after {
                    content: "CONTACT";
                }
            }

            &.active {
                svg{
                    color:#ffd700;
                }                
            }
        }
    }

    ul {
        position: absolute;
        bottom: 20px;
        width: 100%;
        display: block;
        padding: 0; margin: 0;
        list-style: none;
        text-align: center;

        li {
            a {
                padding: 7px 0;
                display: block;
                font-size: 18px;
                line-height: 18px;

                &:hover svg {
                    color:#ffd700;
                }
            }
        }
    }


}